.inner-title { padding-top: 22rem; background-color: var(--color-f5);padding-bottom: 4.5rem; }
.inner-title .innnr_head { text-align: center; }
.inner-title .innnr_head h2 { font: var(--font-18); padding-bottom: 2rem; letter-spacing: 0.72px; color: var(--color-4e); font-weight: 600; }
.inner-title .innnr_head h3 { font: var(--font-45); letter-spacing: -1.35px; color: var(--color-4e); margin-bottom: 3.8rem; }
.inner-title .innnr_head h3>span { color: var(--color-e8); }
.inner-title .innnr_head ul { list-style: none; border-top: 1px solid #ccc; display: flex; justify-content: center; gap: 3rem; margin-bottom: 0;padding-left: 0; }
.inner-title .innnr_head ul li { font: var(--font-14); color: #0000008c;font-weight: bold;    padding-top: 2.6rem; }
.inner-title .innnr_head ul li.active, .inner-title .innnr_head ul li:hover { color: var(--color-00); border-top: 1px solid var(--color-00); }

.about_one { padding-block: 10rem; }
.about_one .abt_cntnt { text-align: center; }
.about_one .abt_cntnt p { padding-bottom: 5rem; font: var(--font-24); font-weight: 300; padding-inline: 1rem; line-height: 1.4; }
.about_one .estblish { text-align: center; padding-block: 4rem; }
.about_one .estblish p { font: var(--font-16); color: var(--color-21); }
.about_one .top_log_grid { display: grid; grid-template-columns: repeat(3, 1fr); gap: 1.9rem; }
.about_one .top_log_grid figure { padding: 2.2rem 4.8rem; border: 1px solid var(--color-d4); }
.about_one .top_log_grid figure img { width: auto; margin-inline: auto; }
.about_one .top_log_grid figure figcaption { text-align: center;padding-top: 0.3rem; }
.about_one .top_log_grid figure figcaption p { font: var(--font-16); color: var(--color-21); }
.about_one .btm_log_grid { display: grid; grid-template-columns: repeat(3, 1fr); gap: 1.5rem; margin-inline: 9%; margin-top: 1.5rem;}
.about_one .btm_log_grid figure { padding: 2.2rem 3.8rem; border: 1px solid var(--color-d4); }
.about_one .btm_log_grid figure img { width: auto; margin-inline: auto; }
.about_one .btm_log_grid figure figcaption { text-align: center;padding-top: 0rem; }
.about_one .btm_log_grid figure figcaption h4 { letter-spacing: -1.9px; font: var(--font-48); font-weight: 400; color: var(--color-73); }
.about_one .btm_log_grid figure figcaption h4 sup { top: -0.2em;letter-spacing: -1.15px; }
.about_one .btm_log_grid figure figcaption p { font: var(--font-14); color: var(--color-21); font-weight: 400; margin-bottom: 0.6rem; }

.about_two {padding-bottom: 5rem;}
.about_two .early_grid { display: grid; grid-template-columns: repeat(1, 49.4% 34.7%); gap: 8rem; }
.about_two .early_grid figure img { border-radius: 0.8rem; }
.about_two .early_grid .early_rgt  {padding-top: 3.2rem;}
.about_two .early_grid .early_rgt h4 { font: var(--font-24); color: var(--color-29); margin-bottom: 2.1rem; }
.about_two .early_grid .early_rgt h5 { font: var(--font-24); font-weight: 300; }
.about_two .early_grid .early_rgt .inst-reg { padding-top: 4rem; }
.about_two .early_grid .early_rgt .inst-reg h5 { font: var(--font-18); color: var(--color-21); border-bottom: 1px solid var(--color-d9); padding-bottom: 1.6rem; margin-bottom: 2.1rem; }
.about_two .early_grid .early_rgt .inst-reg p { font: var(--font-18); color: var(--color-21); margin-bottom: 1.5rem; }
.about_two .early_grid .early_rgt .inst-reg h3 { font: var(--font-34); letter-spacing: -1.02px; color: var(--color-4e); line-height: 1; }
.about_two .early_grid .early_rgt .inst-reg h3>span { color: var(--color-ff); }
.about_two .early_grid .early_rgt .rankbtn {padding-top: 2.4rem; display: flex;gap: 0.6rem; }
.about_two .early_grid .early_rgt .rankbtn :is(.swiper-button-prev,.swiper-button-next) { position: relative; margin-top: 0; width: 2.2rem; height: 2.2rem; right: auto; left: auto; font-size: var(--font-12); font-weight: bold; color: var(--color-55); background-color: transparent; border: 0.1rem solid var(--color-55); transition: all .5s ease; background-repeat: no-repeat;background-position: center; }
.about_two .early_grid .early_rgt .rankbtn .swiper-button-prev{rotate: 180deg;}
.about_two .early_grid .early_rgt .rankbtn .swiper-button-next:after, .about_two .early_grid .early_rgt .rankbtn .swiper-button-prev:after {display: none;}


.about_three {padding-bottom: 10.7rem;}
.about_three .vsn_msn_grid { display: grid; grid-template-columns: repeat(1, 35.6% auto); gap: 8rem; margin-right: -13.8rem; background-color: #fff; padding-left: 13.8rem; margin-left: 13.8rem;position: relative;z-index: 1; }
.about_three .vsn_msn_grid::after { content: ''; position: absolute; background-color: #f6f6f6; width: 100%; height: 50%; z-index: -1; bottom: 0; left: 0; }
.about_three .vsn_msn_grid .miss-rgt { padding-top: 3.8rem; padding-right:7.8rem; }
.about_three .vsn_msn_grid .miss-rgt h4 { font: var(--font-24); color: var(--color-29); margin-bottom: 2.4rem; }
.about_three .vsn_msn_grid .miss-rgt h5 { font: var(--font-24); font-weight: 300; margin-bottom: 3.8rem; }
.about_three .vsn_msn_grid .custom-list li::before { left: -2rem; }
.about_three .vsn_msn_grid .miss-rgt ul { display: grid; grid-template-columns: repeat(2, 1fr); gap: 5rem 3.9rem;padding-left: 2rem; }
.about_three .vsn_msn_grid .custom-list li { border-bottom: 1px dotted #ddd; padding-left: 0; padding-bottom: 1.2rem; }
.about_three .vsn_msn_grid .miss-rgt .msn { margin-top: 9.5rem;position: relative; }
.about_three .vsn_msn_grid .miss-rgt .msn p { font: var(--font-16); color: var(--color-21); margin-bottom: 4rem; }
.about_three .vsn_msn_grid .miss-rgt .msn h6 { font: var(--font-14); font-weight: bold; margin-bottom: 3rem; }
.about_three .vsn_msn_grid .miss-rgt .msn ul { gap: 2.5rem 3.9rem; }


.about_four { position: relative; padding-bottom: 7rem; }
.about_four::after { content: ''; position: absolute; width: 100%; height: 80%; background: #fff; left: 0; bottom: 0; z-index: -1; }
.about_four .section-title { font:var(--font-24);color: var(--color-white); margin-bottom: 3rem; font-weight: 500; }
.about_four .values-section { background-color: var(--color-9a); border-radius: 50px 8px; padding-block: 5.2rem 8.5rem; }
.about_four .values-grid { display: grid; grid-template-columns: repeat(3, 1fr); }
.about_four .value-box { padding-inline: 2rem 4rem; padding-block: 2rem 3rem; display: flex; flex-direction: column; align-items: center; justify-content: center; border:1px solid rgb(255 255 255 / 10%); border-left: none; border-top: none; text-align: center; }
.about_four .values-grid .value-box:nth-child(-n+3) { padding-top: 0; }
.about_four .values-grid > .value-box:nth-last-child(-n+3) { padding-bottom: 2rem; }
.about_four .value-box:nth-child(3n) { border-right: none; }
.about_four .value-box:nth-last-child(-n+3) { border-bottom: none; }
.about_four .value-box h3 { font: var(--font-16); margin-block: 3rem 1rem; color: var(--color-white); }
.about_four .value-box p { font: var(--font-12); line-height: 1.6; color: #d8e6fa; padding-inline: 3rem; }

/* Responsive */
@media (max-width: 900px) {
.about_four .values-grid { grid-template-columns: repeat(2, 1fr); }
.about_four .value-box { border-right:1px solid rgb(255 255 255 / 10%); border-bottom:1px solid rgb(255 255 255 / 10%); }
.about_four .value-box:nth-child(2n) { border-right: none; }
.about_four .value-box:nth-last-child(-n+2) { border-bottom: none; }
}

@media (max-width: 600px) {
.about_four .values-grid { grid-template-columns: 1fr; }
}

.about_five { padding-block: 0rem 10rem; position: relative;}
.about_five::before { content: ''; position: absolute; width: 100%; height: 33%; background: #fff; left: 0; top: 0; z-index: -1; }
.about_five .quality_grid { display: grid; grid-template-columns: 48.7% 48%; gap: 4.9rem; margin-left: 8rem; justify-items: center;align-items: center; }
.about_five .quality_grid figure img { border-radius: 0.8rem; }
.about_five .quality_grid .qty-rgt h3 { font: var(--font-24); color: var(--color-29); margin-bottom: 3rem; }
.about_five .quality_grid .qty-rgt p { font: var(--font-14); padding-bottom: 5.7rem;line-height: 1.5; }
.about_five .quality_grid .qty-rgt h5 { font: var(--font-14); font-weight: bold; margin-bottom: 3rem; }
.about_five .quality_grid .qty-rgt .qlty { padding-right: 8rem; }
.about_five .quality_grid .qty-rgt li { border-bottom: 1px dotted #2121215c; padding-bottom: 1.8rem; margin-bottom: 1.6rem;padding-left: 0; }
.about_five .quality_grid .qty-rgt .custom-list { padding-left: 2rem; }
.about_five .quality_grid .qty-rgt .custom-list li::before { left: -2rem; }



/* leadership page start */
.leadership_one {position: relative;padding-top: .9rem}
.leadership_one::before { content: ''; position: absolute; width: 100%; height: 19%; background: #ddebf5; left: 0; top: 0; z-index: -1; }
.leadership_one .top_img figure { display: grid; align-items: end; position: relative;}
.leadership_one .top_img figure>* { grid-area: 1 / 1; }
.leadership_one .top_img figure:hover figcaption h3, .leadership_one .top_img figure:hover figcaption p{color: var(--color-00);transition: 0.5s ease-in-out;}
.leadership_one .top_img figure:hover figcaption p>img{opacity: 0.5;}
.leadership_one .top_img figure figcaption {padding-left: 6.7rem;padding-bottom: 4.4rem;}
.leadership_one .top_img figure figcaption h3 { font: var(--font-40); letter-spacing: -1.2px; color: var(--color-white); }
.leadership_one .top_img figure figcaption h3>span {display: block;}
.leadership_one .top_img figure figcaption p { display: flex; gap: 1rem; color: var(--color-white); }
.leadership_one .top_img figure figcaption p img { width: 2.2rem; height: 2.2rem; filter: invert(1) brightness(4.5); }

.leadership_two { padding-block: 6rem 10rem; }
.leadership_two  .leadership_grid { display: grid; grid-template-columns: repeat(3, 1fr); gap: 4.8rem 3.4rem; }
.leadership_two .leadership_grid figure { position: relative; }
.leadership_two .leadership_grid figure figcaption { position: relative; padding-left: 4rem;padding-top: 2.2rem; }
.leadership_two .leadership_grid figure figcaption::before { content: ''; position: absolute; width: 6px; height: 132%; background-color: var(--color-00); left: 2rem; bottom: 0; }
.leadership_two .leadership_grid figure figcaption h3 { font: var(--font-26); font-weight: bold; color: var(--color-4e);letter-spacing: -0.87px; }
.leadership_two .leadership_grid figure figcaption p { display: flex; flex-direction: column; font: var(--font-17); color: var(--color-e8); gap: 1rem; }
.leadership_two .leadership_grid figure figcaption p>img { width: 2.2rem; height: 2.2rem; }
.leadership_two .leadership_grid figure:hover figcaption h3, .leadership_two .leadership_grid figure:hover figcaption p{color: var(--color-00);transition: 0.5s ease-in-out;}
.leadership_two .leadership_grid figure:hover figcaption p>img{opacity: 0.5;}
/* leadership page end */

/* leadership details page start */
.leadership_dtls_one {position: relative;padding-top: .9rem}
.leadership_dtls_one::before { content: ''; position: absolute; width: 100%; height: 19%; background: #ddebf5; left: 0; top: 0; z-index: -1; }
.leadership_dtls_one .top_img figure {
    display: grid;
    align-items: center;
    position: relative;
    justify-content: end;
}
.leadership_dtls_one .top_img figure>* { grid-area: 1 / 1; }
.leadership_dtls_one .top_img figure figcaption {
    padding-bottom: 0;
    max-width: 55rem;
    margin-inline: auto 6.2rem;
}
.leadership_dtls_one .top_img figure figcaption .desgtn {
    border-bottom: 1px solid #ffffff73;
    padding-bottom: 1.7rem;
    margin-bottom: 3.6rem;
}
.leadership_dtls_one .top_img figure figcaption h3 { font: var(--font-40); letter-spacing: -1.2px; color: var(--color-white);margin-bottom: 1rem; }
.leadership_dtls_one .top_img figure figcaption h3>span {display: block;}
.leadership_dtls_one .top_img figure figcaption p {color: var(--color-white); }
/* leadership details page end */

